import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import BottomNavigation from '@mui/material/BottomNavigation';

const dispatchedEvents = [];

const SrsMuiBottomNavigation = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <BottomNavigation {...componentProps}>
      <slot />
    </BottomNavigation>
  );
}

const styles = `
  /* Custom styles for the BottomNavigation component */
`;

const props = [
  {
    name: "value",
    defaultValue: null,
    type: "string",
  },
  {
    name: "showLabels",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to BottomNavigation if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiBottomNavigation), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
